
'use client';

import { useState, useEffect } from 'react';
import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { assessJobAutomationRisk } from '@/app/actions';
import type { AutomationRiskAssessmentOutput } from '@/ai/flows/automation-risk-assessment';
import type { JobSubmission } from '@/types';

import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, ShieldAlert, AlertCircle, History, Trash2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import Link from 'next/link';

const formSchema = z.object({
  jobDescription: z.string().min(1, { message: 'Job description cannot be empty.' }).max(5000, { message: 'Job description is too long.'}),
});

type FormData = z.infer<typeof formSchema>;

const JOB_HISTORY_KEY = 'jobSubmissionHistory';
const MAX_HISTORY_ITEMS = 5;

export function AutomationRiskForm() {
  const [isLoading, setIsLoading] = useState(false);
  const [assessmentResult, setAssessmentResult] = useState<AutomationRiskAssessmentOutput | null>(null);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();
  const [jobHistory, setJobHistory] = useState<JobSubmission[]>([]);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const storedHistory = localStorage.getItem(JOB_HISTORY_KEY);
      setJobHistory(storedHistory ? JSON.parse(storedHistory) : []);
    }
  }, []);

  const addToHistory = (jobDescription: string) => {
    const newSubmission: JobSubmission = {
      id: new Date().toISOString(),
      jobDescription,
      timestamp: new Date().toLocaleString(),
    };
    const updatedHistory = [newSubmission, ...jobHistory].slice(0, MAX_HISTORY_ITEMS);
    setJobHistory(updatedHistory);
    localStorage.setItem(JOB_HISTORY_KEY, JSON.stringify(updatedHistory));
     // Dispatch event for homepage to update insights
    window.dispatchEvent(new CustomEvent('jobHistoryUpdated'));
  };

  const clearHistory = () => {
    setJobHistory([]);
    localStorage.removeItem(JOB_HISTORY_KEY);
    toast({
      title: "History Cleared",
      description: "Your job assessment history has been cleared.",
    });
     window.dispatchEvent(new CustomEvent('jobHistoryUpdated'));
  };


  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      jobDescription: '',
    },
  });

  const onSubmit: SubmitHandler<FormData> = async (data) => {
    setIsLoading(true);
    setAssessmentResult(null);
    setError(null);
    try {
      const result = await assessJobAutomationRisk(data);
      setAssessmentResult(result);
      addToHistory(data.jobDescription);
      toast({
        title: "Assessment Complete",
        description: "Automation risk has been assessed. Your personalized insights on the homepage may be updated.",
      });
    } catch (e: any) {
      setError(e.message || 'An unexpected error occurred.');
      toast({
        title: "Error",
        description: e.message || "Failed to assess risk. Please try again.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };
  
  const getRiskLevelStyles = (riskLevel: string | undefined) => {
    if (!riskLevel) return { iconColor: "text-muted-foreground", bgColor: "bg-muted", textColor: "text-muted-foreground" };
    switch (riskLevel.toLowerCase()) {
      case 'low':
        return { iconColor: "text-green-600", bgColor: "bg-green-100 dark:bg-green-900/30", textColor: "text-green-700 dark:text-green-400" };
      case 'medium':
        return { iconColor: "text-yellow-600", bgColor: "bg-yellow-100 dark:bg-yellow-900/30", textColor: "text-yellow-700 dark:text-yellow-400" };
      case 'high':
        return { iconColor: "text-red-600", bgColor: "bg-red-100 dark:bg-red-900/30", textColor: "text-red-700 dark:text-red-400" };
      default:
        return { iconColor: "text-muted-foreground", bgColor: "bg-muted", textColor: "text-muted-foreground" };
    }
  };

  return (
    <div className="space-y-6">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl text-primary flex items-center">
            <ShieldAlert className="mr-2 h-6 w-6" />
            Assess Automation Risk
          </CardTitle>
          <CardDescription>
            Enter a job description below to analyze its potential automation risk. Your submissions will help generate <Link href="/" className="text-accent hover:underline">personalized career insights</Link> on the homepage.
          </CardDescription>
        </CardHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)}>
            <CardContent className="space-y-4">
              <FormField
                control={form.control}
                name="jobDescription"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel htmlFor="jobDescription" className="text-lg">Job Description</FormLabel>
                    <FormControl>
                      <Textarea
                        id="jobDescription"
                        placeholder="Paste the full job description here (1 to 5000 characters)..."
                        rows={10}
                        className="resize-none"
                        {...field}
                        disabled={isLoading}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </CardContent>
            <CardFooter>
              <Button type="submit" disabled={isLoading} className="w-full sm:w-auto">
                {isLoading ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Assessing...
                  </>
                ) : (
                  'Assess Risk'
                )}
              </Button>
            </CardFooter>
          </form>
        </Form>
      </Card>

      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertTitle>Error</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {assessmentResult && (
        <Card className="shadow-lg animate-in fade-in duration-500">
          <CardHeader>
            <CardTitle className="text-xl text-primary">Assessment Result</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
               <div className={`p-3 rounded-md ${getRiskLevelStyles(assessmentResult.riskLevel).bgColor}`}>
                <h3 className={`text-lg font-semibold ${getRiskLevelStyles(assessmentResult.riskLevel).textColor}`}>
                  Risk Level: {assessmentResult.riskLevel}
                </h3>
              </div>
            </div>
            <div>
              <h3 className="font-semibold text-md text-primary">Risk Factors:</h3>
              <p className="text-muted-foreground whitespace-pre-wrap">{assessmentResult.riskFactors}</p>
            </div>
            <div>
              <h3 className="font-semibold text-md text-primary">Summary:</h3>
              <p className="text-muted-foreground whitespace-pre-wrap">{assessmentResult.summary}</p>
            </div>
          </CardContent>
        </Card>
      )}

      {jobHistory.length > 0 && (
        <Card className="shadow-md">
          <CardHeader className="flex flex-row items-center justify-between">
            <div className="flex items-center">
              <History className="mr-2 h-5 w-5 text-primary" />
              <CardTitle className="text-lg text-primary">Recent Assessments</CardTitle>
            </div>
            <Button variant="outline" size="sm" onClick={clearHistory} className="text-destructive hover:bg-destructive/10">
              <Trash2 className="mr-1 h-4 w-4" />
              Clear History
            </Button>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2">
              {jobHistory.map(item => (
                <li key={item.id} className="text-sm text-muted-foreground p-2 border rounded-md">
                  <span className="font-medium text-foreground block">Assessed on: {item.timestamp}</span>
                  {item.jobDescription.substring(0, 100)}...
                </li>
              ))}
            </ul>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
