// src/app/actions.ts
"use server";

import { assessAutomationRisk as assessAutomationRiskFlow, type AutomationRiskAssessmentInput, type AutomationRiskAssessmentOutput } from "@/ai/flows/automation-risk-assessment";
import { identifyFutureSkills as identifyFutureSkillsFlow, type IdentifyFutureSkillsInput, type IdentifyFutureSkillsOutput } from "@/ai/flows/future-skills-identifier";
import { getCareerRecommendations as getCareerRecommendationsFlow, type CareerRecommendationsInput, type CareerRecommendationsOutput } from "@/ai/flows/career-recommendations-flow";
import type { JobSubmission } from '@/types';


export async function assessJobAutomationRisk(
  input: AutomationRiskAssessmentInput
): Promise<AutomationRiskAssessmentOutput> {
  try {
    const result = await assessAutomationRiskFlow(input);
    return result;
  } catch (error) {
    console.error("Error assessing automation risk:", error);
    throw new Error("Failed to assess automation risk. Please try again.");
  }
}

export async function identifyRelevantFutureSkills(
  input: IdentifyFutureSkillsInput
): Promise<IdentifyFutureSkillsOutput> {
  try {
    const result = await identifyFutureSkillsFlow(input);
    return result;
  } catch (error) {
    console.error("Error identifying future skills:", error);
    throw new Error("Failed to identify future skills. Please try again.");
  }
}

export type PersonalizedInsightsOutput = CareerRecommendationsOutput;

export async function getPersonalizedInsights(
  jobHistory: JobSubmission[]
): Promise<PersonalizedInsightsOutput> {
  try {
    const input: CareerRecommendationsInput = { jobHistory };
    const result = await getCareerRecommendationsFlow(input);
    return result;
  } catch (error) {
    console.error("Error getting personalized insights:", error);
    // It's better to return a structured error or a default state
    // than to throw a generic error that breaks the client.
    return {
      overallObservations: "We encountered an issue generating personalized insights. Please try again later.",
      careerRecommendations: [],
      learningPrograms: [],
      suggestedReminders: [],
      nextSteps: ["Try assessing another job description or check back soon."],
    };
  }
}
